import React, {useEffect, useState} from 'react'
import Glide from '@glidejs/glide'
import MenuProducts from './menu-products/MenuProducts'
import MenuSettings from './menu-products/MenuSettings'
import MenuOptions from './menu-products/MenuOptions'
import menu from '../../json/menu.json'

export default function TopProducts() {

    const [isActive, setActive] = useState(1)
    const [jsonMenu] = useState(menu)

    function toggleActive(index){
        setActive(index)
    }

    let slider = new Glide('.menu', {
        type: 'slider',
        perView: 20,
        peek:{
            before:0,
            after:0
        },
        rewind:false,
        bound:true,
        animationTimingFunc:"ease",
        animationDuration:1000,
        swipeThreshold:50,
        dragThreshold:10,
        breakpoints:{
            360:{
                perView:6
            },
            640:{
                perView:8
            },
            768:{
                perView:8
            },
            1024:{
                perView:8
            }
        }
    })

    useEffect(() => {
        slider.mount()
    }, [slider])

    return (
        <div className="container mx-auto px-4">
            <div className="w-full">
                <h1 className="font-nunito font-bold text-2xl text-center">
                    Top Products
                </h1>
                <h1 className="text-center text-sm font-nunito my-2 mx-12 md:mx-auto">
                    We source directly from multiple factories to ensure the best quality and lowest prices.
                </h1>
                <div className="glide menu container mx-auto overflow-x-hidden">
                    <div className="glide__track" data-glide-el="track">
                        <ul className="glide__slides flex mb-0 list-none md:flex-row pt-8 pb-4" role="tablist">
                            {jsonMenu.map((item, i) => {
                                const index = i+1
                                return(
                                    <li key={index} onClick={() => toggleActive(index)} className="glide__slide mr-2 flex-auto text-center">
                                        <a role="tablist"
                                            className={`${isActive === item.id ? ("border-b-2 border-green-500 text-black") : ("border-none text-gray-300")} flex justify-center h-12 w-16 text-xs font-bold uppercase shadow-lg rounded`}>
                                            <svg className={`${isActive === item.id ? ("border-green-500") : ("border-transparent") } border h-10 w-10 rounded-md p-1 `}
                                                fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                                                <path fill-rule="evenodd" d={item.icon} clip-rule="evenodd"  />
                                            </svg>
                                        </a>
                                    </li>
                                )
                            })}
                        </ul>
                    </div>
                </div>
                <div className="py-5">
                    {
                        (isActive === 1 ? <MenuProducts/> : 
                            (isActive === 2 ? <MenuSettings/> : 
                                (isActive === 3 ? <MenuOptions/> : <MenuOptions/>)
                            )
                        )
                    }
                </div>
            </div>
        </div>
    )
}