import React from 'react'

export default function CardProduct(props) {

    let {
        activeClass,
        btnActive,
        title,
        region,
        price,
        stock,
        grosir,
        shipping
    } = props

    return (
        <div className={activeClass}>
            <img className="h-64 w-full rounded-md"
                src={`${process.env.PUBLIC_URL}/images/item.jpg`}
            />
            <div className="mt-2">
                <h1 className="font-nunito text-xs font-bold">{title}</h1>
                <h1 className="font-nunito text-xs text-gray-400">
                    From <span className="text-green-500">{region}</span>
                </h1>
            </div>
            <div className="grid grid-cols-2 my-2 font-openSans text-xs">
                <div className="">
                    <h1 className="text-gray-400">
                        Price (usd)
                    </h1>
                    <h1>
                        ${price}
                    </h1>
                </div>
                <div className="w-full">
                    <h1 className="flex justify-end text-gray-400">
                        Inventory
                    </h1>
                    <h1 className="flex justify-end">
                        {stock} pcs
                    </h1>
                </div>
            </div>
            <div className="grid grid-cols-2 font-openSans text-xs">
                <div className="">
                    <h1 className="text-gray-400">
                        Shipping Price
                    </h1>
                    <h1>
                        ${grosir}
                    </h1>
                </div>
                <div className="w-full">
                    <h1 className="flex justify-end text-gray-400">
                        Shipping Time
                    </h1>
                    <h1 className="flex justify-end">
                        {shipping} Days
                    </h1>
                </div>
            </div>
            <div className="mx-2 mt-4">
            {
                btnActive ? 
                    <button className="bg-green-500 text-white uppercase w-full py-1 rounded-md">
                        Buy
                    </button> 
                : null
            }
            </div>
        </div>
    )
}
