import React, {useEffect, useState} from 'react'
import Glide from '@glidejs/glide'
import CardService from '../card/CardService'
import product from '../../../json/product.json'

export default function MenuServices() {

    let [isActive, setActive] = useState(1)
    let [jsonProduct] = useState(product)

    function toggleActive(index){
        setActive(index)
    }

    let slider = new Glide('.services', {
        type: 'slider',
        perView: 6,
        peek:{
            before:0,
            after:0
        },
        rewind:false,
        bound:true,
        animationTimingFunc:"ease",
        animationDuration:1000,
        swipeThreshold:50,
        dragThreshold:10,
        breakpoints:{
            360:{
                perView:2
            },
            640:{
                perView:2
            },
            768:{
                perView:4
            },
            1024:{
                perView:5
            }
        }
    })

    useEffect(() => {
        slider.mount()
    }, [slider])

    return(
        <div className="glide services container mx-auto overflow-x-auto">
            <div className="glide__track" data-glide-el="track">
                <div className="glide__slides flex gap-2">
                    {jsonProduct.map((item, i) => {
                        const index = i+1
                        return(
                            <CardService/>

                        )
                    })}
                </div>
            </div>
        </div>
    )
}