import React, {useEffect, useState} from 'react'
import Glide from '@glidejs/glide'
import CardProduct from '../card/CardProduct'
import product from '../../../json/product.json'

export default function MenuProducts() {

    let [isActive, setActive] = useState(1)
    let [jsonProduct] = useState(product)

    function toggleActive(index){
        setActive(index)
    }

    let slider = new Glide('.products', {
        type: 'slider',
        perView: 6,
        peek:{
            before:0,
            after:0
        },
        rewind:false,
        bound:true,
        animationTimingFunc:"ease",
        animationDuration:1000,
        swipeThreshold:50,
        dragThreshold:10,
        breakpoints:{
            360:{
                perView:2
            },
            640:{
                perView:2
            },
            768:{
                perView:4
            },
            1024:{
                perView:5
            }
        }
    })

    useEffect(() => {
        slider.mount()
    }, [slider])

    return(
        <div className="glide products container mx-auto overflow-x-auto">
            <div className="glide__track" data-glide-el="track">
                <div className="glide__slides flex">
                    {jsonProduct.map((item, i) => {
                        const index = i+1
                        return(
                            <div onClick={() => toggleActive(index)}>
                                <CardProduct key={index}
                                    title={item.merk}
                                    region={item.negara}
                                    price={item.harga}
                                    stock={item.stok}
                                    grosir={item.grosir}
                                    shipping={item.pengiriman}
                                    activeClass={`${isActive === item.id ? 
                                        ("border-2 border-green-500 glide__slide flex-row mx-auto mb-2 p-1 rounded-md") 
                                        : ("border-2 border-transparent glide__slide flex-row mx-auto mb-2 p-1 rounded-md")}`}
                                    btnActive={isActive === item.id}
                                />
                            </div>

                        )
                    })}
                </div>
            </div>
        </div>
    )
}